import Entity from './Entity.js';
import Sprite from '../enums/Sprite.js';

export default class Smoke extends Entity {
    constructor(x, y) {
        super(x, y);

        this.sprite = Sprite.SMOKE;
        this.life = 5;
        this.physics = false;
    }

    update() {
        super.update();

        this.direction += 0.01;
        this.y -= 0.1;

        this.life -= 0.1;
        if (this.life <= 0) {
            this.alive = false;
        }
    }
}